.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    padding: 20px;
    font-family: sans-serif;
    background-color: #1a1a1a;
}

.logo{
    margin-left: 60px;
    font-size: 30px;
    color: cornflowerblue;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.header form input{
    width: 20vw;
    padding: 15px 0;
    margin-right: 20px;
    outline: none;
    font-size: 15px;
}

.header form button{
    background-color: cornflowerblue;
    color: #FFF;
    font-size: 1em;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    padding: 13px;
    width: 8em;
    margin-right: 70px;
    cursor: pointer;
    transition: 1s;
}
.header form button:hover{
    opacity: 0.7;
}

@media (max-width: 850px){
    .logo{
        display: none;
    }
    .header form input{
        width: 60vw;
        margin: 0;
        margin-right: 10px;
    }
    .header form button{
        width: 20vw;
        margin: 0;
    }
    .header{
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media(max-width: 400px){
    .header form button{
        font-size: 10px;
        padding: 18px;
    }
}