.cardTemporada{
    display: flex;
    width: 90%;
    border-bottom: 1px solid #646466;
    padding: 1rem 0;
}

.cardTemporada img{
    max-width: 100px;
    max-height: 150px;
    border-radius: 10px;
    margin-top: 1.5rem;
}

.temporadaInfo{
    color: #c4c4c4;
    margin-left: 2rem;
    font-size: 22px;
}

.temporadaInfo h3{
    color: cornflowerblue;
}

.qtdEps{
    font-weight: bold;
}